@use "../abstracts/" as *;

.module {
	width: 100vw;
	display: grid;
	align-items: center;
	align-content: start;
	@media (min-width: $breakpoint-2xl) {
		width: 1440px;
		grid-template-columns: 1fr 1fr;
		gap: 95px;
		margin-top: 96px;
	}
}

.module__content {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	min-height: 220px;
	padding-inline: 1rem;
	@media (min-width: $breakpoint-2xl) {
		justify-self: left;
		max-width: 380px;
		height: 250px;
		padding-inline: 0;
	}
}

.module__image {
	@media (min-width: $breakpoint-2xl) {
		max-width: 505px;
		height: 300px;
		justify-self: right;
	}
}

.module__button {
	cursor: pointer;
	width: 128px;
	height: 48px;
	padding: 16px 40px 16px 40px;
	background-color: $color-primary;
	border: none;
	border-radius: 32px;
	font-family: $font;
	font-size: 1rem;
	font-weight: 700;
	color: #fff;
	text-align: center;
}

.module__title {
	font-family: $font;
	font-style: normal;
	font-size: 2.625rem;
	font-weight: 700;
	color: $color-dark;
}

.module__text {
	font-family: $font;
	font-style: normal;
	font-size: 1rem;
	font-weight: 300;
	color: $color-dark;
	text-align: justify;
	line-height: 150%;
}

.module__popup {
	display: none;
	place-items: center;
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	overflow: hidden;
	background-color: rgba($color-dark, 0.5);
}

.module__content--popup {
	justify-self: center;
	display: grid;
	grid-template-columns: 1fr 1fr;
	row-gap: 24px;
	padding: 56px 46px 56px 95px;
	max-width: 920px;
	width: 920px;
	height: 210px;

	background-color: #fff;
}

.module__close-button {
	cursor: pointer;
	position: relative;
	margin-top: -10px;
	justify-self: right;
	width: 12px;
	height: 12px;
	background: none;
	border: none;
	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 50%;
		width: 2px;
		height: 12px;
		transform: rotate(45deg);
		background-color: #000;
	}
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 50%;
		width: 2px;
		height: 12px;
		transform: rotate(-45deg);
		background-color: #000;
	}
}

.module__text--semibold {
	font-weight: 600;
}

.module__button--reset {
	display: none;
	justify-self: right;
}
